:root{--primary-color: #007bff;--primary-dark: #0056b3;--text-color: #333;--text-light: #666;--background-light: #f8f9fa;--white: #ffffff;--shadow: 0 10px 30px rgba(0, 0, 0, 0.1)}@media(prefers-color-scheme: dark){:root{--background: #0a0a0a;--foreground: #ededed}}html,body{max-width:100vw;overflow-x:hidden;font-family:var(--font-inter)}body{color:var(--text-color);background:var(--background-light);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box;padding:0;margin:0}a{color:inherit;text-decoration:none}button{cursor:pointer;font-family:inherit}input,textarea{font-family:inherit}::-webkit-scrollbar{width:8px}::-webkit-scrollbar-track{background:var(--background-light)}::-webkit-scrollbar-thumb{background:var(--primary-color);border-radius:4px}::-webkit-scrollbar-thumb:hover{background:var(--primary-dark)}@keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes slideUp{from{transform:translateY(20px);opacity:0}to{transform:translateY(0);opacity:1}}.container{max-width:1200px;margin:0 auto;padding:0 2rem}.text-center{text-align:center}.mt-1{margin-top:.5rem}.mt-2{margin-top:1rem}.mt-3{margin-top:1.5rem}.mt-4{margin-top:2rem}.mt-5{margin-top:2.5rem}.mb-1{margin-bottom:.5rem}.mb-2{margin-bottom:1rem}.mb-3{margin-bottom:1.5rem}.mb-4{margin-bottom:2rem}.mb-5{margin-bottom:2.5rem}@media(max-width: 768px){.container{padding:0 1rem}}@media(prefers-color-scheme: dark){html{color-scheme:dark}}